"use client";

import { useState } from "react";
import { Search, ChevronDown } from "lucide-react";

const BranchLocator = () => {
  const [selectedState, setSelectedState] = useState("");
  const [selectedBranch, setSelectedBranch] = useState("");
  const [states] = useState([
    "Maharashtra",
    "Karnataka",
    "Tamil Nadu",
    "Delhi",
    "Gujarat",
    "Rajasthan",
    "Uttar Pradesh",
    "West Bengal",
  ]);
  
  const [branches, setBranches] = useState([]);
  
  const branchData = {
    "Maharashtra": ["Mumbai Central", "Pune City", "Nagpur Main", "Thane West"],
    "Karnataka": ["Bengaluru Downtown", "Mysore Central", "Hubli Main"],
    "Tamil Nadu": ["Chennai Central", "Coimbatore Main", "Madurai South"],
    "Delhi": ["Connaught Place", "Dwarka", "Rohini"],
    "Gujarat": ["Ahmedabad Main", "Surat City", "Vadodara Central"],
    "Rajasthan": ["Jaipur Main", "Udaipur City", "Jodhpur Central"],
    "Uttar Pradesh": ["Lucknow Central", "Kanpur Main", "Varanasi South"],
    "West Bengal": ["Kolkata Central", "Howrah Main", "Siliguri City"],
  };

  const handleStateChange = (state) => {
    setSelectedState(state);
    setSelectedBranch("");
    setBranches(branchData[state] || []);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!selectedState || !selectedBranch) {
      alert("Please select both state and branch");
      return;
    }
    alert(`Finding nearest branch: ${selectedBranch}, ${selectedState}`);
  };

  return (
    <div className="relative py-8 md:py-10 px-4 sm:px-6 shadow-md">
      {/* Background Image with Overlay */}
      <div className="absolute inset-0 z-0">
        <div className="absolute inset-0 bg-[url('/images/celebration.jpg')] bg-cover bg-center opacity-20"></div>
        <div className="absolute inset-0 bg-gradient-to-br from-primary/20 via-dark to-primary/70"></div>
      </div>

      <div className="max-w-7xl mx-auto relative z-10">
        <div className="flex flex-col lg:flex-row items-start lg:items-center justify-between gap-8 lg:gap-12">
          
          {/* Left Section */}
          <div className="w-full lg:w-1/2">
            <div className="inline-flex items-center justify-center w-16 h-16 bg-primary/10 rounded-full mb-6">
              <img 
                src="/images/location.gif" 
                alt="Location Pin"
                className="w-12 h-12 rounded-full object-contain"
              />
            </div>
            
            <h2 className="text-2xl sm:text-3xl font-bold text-primary mb-4">
              Check the desired chit at your nearest branch
            </h2>
            
           
          </div>

          {/* Right Section - Form */}
          <div className="w-full lg:w-1/2">
            <div className="bg-transperant backdrop-blur-sm rounded-2xl  ">
              <form onSubmit={handleSubmit} className="space-y-6">
                
                {/* State Selection */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Select your State
                  </label>
                  <div className="relative">
                    <select
                      value={selectedState}
                      onChange={(e) => handleStateChange(e.target.value)}
                      className="w-3/4 px-4 py-3 pr-10 bg-gray-50  rounded-md focus:ring-2 focus:ring-primary/20 focus:border-primary outline-none transition-all appearance-none"
                      required
                    >
                      <option value="">Choose a state</option>
                      {states.map((state) => (
                        <option key={state} value={state}>
                          {state}
                        </option>
                      ))}
                    </select>
                    <ChevronDown className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 pointer-events-none" />
                  </div>
                </div>

                {/* Branch Selection */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Select your Branch
                  </label>
                  <div className="relative">
                    <select
                      value={selectedBranch}
                      onChange={(e) => setSelectedBranch(e.target.value)}
                      disabled={!selectedState}
                      className={`w-3/4 px-4 py-3 pr-10 rounded-md  transition-all appearance-none ${
                        !selectedState
                          ? "bg-gray-100 border-gray-200 text-gray-400 cursor-not-allowed"
                          : "bg-gray-50 border-primary/20 focus:ring-2 focus:ring-primary/20 focus:border-primary outline-none transition-all appearance-none"
                      }`}
                      required
                    >
                      <option value="">Choose a branch</option>
                      {branches.map((branch) => (
                        <option key={branch} value={branch}>
                          {branch}
                        </option>
                      ))}
                    </select>
                    <ChevronDown className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 pointer-events-none" />
                  </div>
                </div>

                {/* Submit Button */}
                <button
                  type="submit"
                  className="w-3/4 py-3.5 bg-primary text-white font-medium rounded-xl hover:bg-primary/90 active:scale-[0.98] transition-all duration-200 flex items-center justify-center gap-2 shadow-lg shadow-primary/20"
                >
                  <Search className="w-5 h-5" />
                  Submit
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BranchLocator;