"use client";

import { Youtube, Linkedin, Instagram, Facebook } from "lucide-react";

export default function TopBar() {
  const activeCount = "224459"; // you can make dynamic later

  return (
    <div className="w-full bg-gradient-to-r from-primary/60 to-primary text-white py-2">
      <div className="container mx-auto flex items-center justify-between px-4">

        {/* Left Section: Active Members */}
        <div className="flex items-center gap-3">
          <span className="font-medium">Active members:</span>

          {/* DIGITS */}
          <div className="flex gap-1">
            {activeCount.split("").map((digit, i) => (
              <span
                key={i}
                className="bg-white text-green-800 font-bold px-2 py-1 rounded-md text-sm"
              >
                {digit}
              </span>
            ))}
          </div>
        </div>

        {/* Right Section: Social Icons */}
        <div className="flex items-center gap-4 opacity-80 hover:opacity-100 transition">
          <Youtube className="cursor-pointer" size={20} />
          <Linkedin className="cursor-pointer" size={20} />
          <Instagram className="cursor-pointer" size={20} />
          <Facebook className="cursor-pointer" size={20} />
        </div>

      </div>
    </div>
  );
}
